body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.nav {
  background-color: #336B87;
}

nav.nav .brand-logo.right {
  color: white;
  margin-right: 2vw;
}

div.nav-wrapper ul.nav a:hover {
  background-color: 763626;
}

ul#nav-mobile {
  background-color: #00bcd4;
}

.navLink {
  color: white;
  font-size: 24px;
  
}
.welcomeText {
  position: absolute;
  top: 10vw;
}

.welcome button.btn {
  position: relative;
  top: -20vw;
  margin: 0 2vw;
  background-color: #336B87;
}

a.navLink:hover {
  background-color: #763626;
}

div.nav-wrapper ul a:hover {
  background-color: #763626;
}


.appointmentContainer {
  margin: 0 6vw;
  box-shadow:2px 0px 6px #00000087;
  transition: margin 300ms;
  -webkit-transition: margin 300ms;
  -moz-transition: margin 300ms;
  -ms-transition: margin 300ms;
  -o-transition: margin 300ms;
}

.appointmentContainer:hover {
  margin: .5vw 5.5vw;
  cursor: pointer;
}

.appointmentBody {
  height: 8vh;
  font-size: 4vh;
  border-radius: 2px;
  -webkit-border-radius: 2px;
  -moz-border-radius: 2px;
  -ms-border-radius: 2px;
  -o-border-radius: 2px;
  margin: 2vw 2vw;
  padding-top: 0.4vw;
  color: white;
  transition: background-color 500ms;
  -webkit-transition: background-color 500ms;
  -moz-transition: background-color 500ms;
  -ms-transition: background-color 500ms;
  -o-transition: background-color 500ms;
}

.newAppointment {
  background-color: #336B87;
}

.editedAppointment {
  background-color: #763626;
}

.appointmentBody:hover {
  background-color: #763626;
  color: white;
  cursor: pointer;
}

.appointment {
  background-color: #90AFC5;
  display: flex;
  align-content: space-between;
  justify-content: center;
  flex-wrap: wrap;
}

.name {
  width: 35vw;
}

.time {
  width: 15vw;
}

.phoneNumber {
  width: 20vw
}

@media only screen and (max-width: 800px) {
  .appointmentContainer:hover {
    margin: .5vw 6.9vw;
    cursor: pointer;
  }
}
.timeTable {
  margin-top: 5%;
  color: black;
  display: flex;
  align-content: space-between;
  justify-content: center;
  flex-wrap: wrap;
  margin: 2vw 6vw;
}

.appointmentHead {
  height: 5vh;
  font-size: 4vh;
}

.appointmentHead.name {
  width: 35vw;
  margin-right: 5vw;
}

.appointmentHead.phoneNumber {
  width: 20vw;
  margin-right: 4vw;
}

.appointmentHead.time {
  width: 15vw;
}

.guestMode {
  color: black;
  text-align: right;
  font-size: 1.3vw;
  position: absolute;
  right: 3vw;
}

#closeModal {
  background-color: #763626;
  margin-right: 2vw;
}

#updateModal {
  background-color: #336B87;
  margin-right: 2vw;
}

label {
  display: block;
}

div.col label.active {
  min-width: 16vw;
}

#appointmentReset {
  background-color: #763626;
  margin-top: 4vw;
}

button#closeConfirmationModal.waves-effect {
  background-color: #763626;
  margin-right: 2vw;
}

button#confirmConfirmationModal.waves-effect {
  background-color: #336B87;
  margin-right: 2vw;

}
#signUpSignIn {
  margin: 5vw auto 0 auto;
  text-align: center
}

.choice {
  font-size: 6vh;
  margin-bottom: 2vh;
}

#password, #email {
  width: 20vw;
}

#authSubmit {
  background-color: #336b87;

}

label.active {
  width: 25vw;
}

