.welcome a {
  color: white;
  font-weight: 500;
}

ul#nav-mobile{
  background-color: #336b87;
}

div.welcome button {
  padding: 0;
}

div.welcome button a {
  display: block;
  width: 10vw;
}

div.welcome button:hover {
