.timeTable {
  margin-top: 5%;
  color: black;
  display: flex;
  align-content: space-between;
  justify-content: center;
  flex-wrap: wrap;
  margin: 2vw 6vw;
}

.appointmentHead {
  height: 5vh;
  font-size: 4vh;
}

.appointmentHead.name {
  width: 35vw;
  margin-right: 5vw;
}

.appointmentHead.phoneNumber {
  width: 20vw;
  margin-right: 4vw;
}

.appointmentHead.time {
  width: 15vw;
}
